import React from 'react';
import {NavLink} from "react-router-dom";
import {Container} from "@mui/material";

const TextInfoPartTwo = () => {
    return (

     <Container>
         <div className='content-article-Leatherhead'>
             <p className='style-global-text-info'>
                 I and my wife stood amazed. Then I realised that the crest of Maybury Hill must be
                 within range of the Martians’ Heat-Ray now that the college was cleared out of
                 the way. At that I gripped my wife’s arm, and without ceremony ran her out into the road.
                 Then I fetched out the servant, telling her I would go upstairs myself for the box she was
                 <NavLink className='hover-link' to='/'>clamouring</NavLink> for.<br/><br/>

                 Leatherhead! I shouted above the sudden noise. She looked away from me downhill.
                 The people were coming out of their houses, astonished. “How are we to get to <strong>Leatherhead?”</strong> she said..</p>
         </div>
     </Container>
    );
};

export default TextInfoPartTwo;