import React from 'react';

const SectionLeatherheadClassicRl = () => {
    return (
        <div className='SectionLeatherheadClassicRl'>
            <div className='content-post-leatherhead max-width-articles-leathead-important'>

                <p className='style-global-text-info padding-bottom-10'>
                    By the light of the now brilliant moons I saw that he was but a shadow of his former self,
                    and as he turned from my caress and commenced greedily to devour the dead carcass at my feet
                    I realized that the poor fellow was more than half starved. I, myself, was in but
                    little better plight but I could not bring myself to eat the uncooked flesh and
                    I had no means of making a fire. When Woola had finished his meal I again took
                    up my weary and seemingly endless wandering in quest of the elusive waterway.
                </p>
                <div className='border-paragraph margin-40-0'>
                    <p className='style-global-text-info'>At daybreak of the fifteenth day of my search
                        <span className='style-global-text-info-2'>John Lusco</span>
                    </p>
                </div>
                <p className='style-global-text-info padding-top-20'>For two days I waited there for Kantos Kan, but as he did not come I started off on foot in a
                    northwesterly direction toward a point where he had told me lay the nearest waterway.
                    My only food consisted of
                    vegetable milk from the plants which gave so bounteously of this priceless fluid.</p>

                <div className='list-articles margin-bottom-30'>

                    <ul style={{paddingLeft:'29px'}} className='padding-bottom style-global-text-info '>
                        <li>Several times I was attacked by wild beasts; strange,
                            uncouth monstrosities that leaped upon me in the dark,
                            so that I had ever to grasp my long-sword in my hand that I might be ready for them.</li>
                        <li>Usually my strange, newly acquired telepathic power warned me in ample time,
                            but once I was down with vicious fangs at my jugular and a
                            hairy face pressed close to mine before I knew that I was even threatened.</li>

                    </ul>
                    <p className='style-global-text-info margin-bottom-30'>
                        What manner of thing was upon me I did not know, but that it was large and heavy and many-legged
                        I could feel. My hands were at its throat before the fangs had a chance to bury themselves in my neck
                        , and slowly
                        I forced the hairy face from me and closed my fingers, vise-like, upon its windpipe.
                    </p>
                    <p style={{paddingLeft:'5px'}} className='padding-bottom-20  style-global-text-info'>
                        1-Without sound we lay there, the beast exerting every effort to reach me with those awful fangs, and<br/>
                        2-I straining to maintain my grip and choke the life from it as I kept it from my throat.<br/>
                        3-Slowly my arms gave to the unequal struggle, and inch by inch the burning eyes and gleaming tusks of my antagonist crept toward me, until, as the hairy face touched mine again,<br/>
                        4-I realized that all was over. And then a living mass of destruction sprang from the surrounding darkness full upon the creature that held me pinioned to the ground.<br/>
                        5-The two rolled growling upon the moss, tearing and rending one another in a frightful manner, but it was soon over and my preserver stood with lowered head above the throat of the dead thing which would have killed me.<br/>
                    </p>
                </div>
            </div>
        </div>
    );
};

export default SectionLeatherheadClassicRl;